import React from 'react';
import { Link } from 'react-router-dom';
import { Container, Row, Col, Card } from 'react-bootstrap';

// 
const predictionOptions = [
  {
    id: 'kcat',
    title: 'Kcat',
    description: '(Kcat)',
    icon: '',
    color: '#4285F4',
    path: '/predict/kcat'
  },
  {
    id: 'km',
    title: 'Km',
    description: '(Km)',
    icon: '',
    color: '#EA4335',
    path: '/predict/km'
  },
  {
    id: 'tm',
    title: 'Tm',
    description: '(Tm)',
    icon: '',
    color: '#FBBC05',
    path: '/predict/tm'
  },
  {
    id: 'solubility',
    title: '',
    description: '',
    icon: '',
    color: '#34A853',
    path: '/predict/solubility'
  }
];

const Home: React.FC = () => {
  return (
    <Container>
      <div className="mb-5 text-center">
        <h1 className="display-4 mb-3"></h1>
        <p className="lead">
          
        </p>
      </div>

      <Row className="g-4">
        {predictionOptions.map(option => (
          <Col key={option.id} md={6} lg={3}>
            <Link to={option.path} className="text-decoration-none">
              <Card className="protein-card border-0 shadow-sm h-100">
                <div className="card-img-container">
                  <span className="protein-icon display-1">{option.icon}</span>
                </div>
                <Card.Body>
                  <Card.Title style={{ color: option.color }}>{option.title}</Card.Title>
                  <Card.Text className="text-muted">{option.description}</Card.Text>
                </Card.Body>
              </Card>
            </Link>
          </Col>
        ))}
      </Row>

      <div className="mt-5 pt-4 text-center">
        <h2></h2>
        <p className="text-muted">
          
        </p>
      </div>
    </Container>
  );
};

export default Home; 