import React, { useState } from 'react';
import { Container, Row, Col, Card, Spinner } from 'react-bootstrap';
import SequenceInput from '../components/SequenceInput';
import ResultDisplay from '../components/ResultDisplay';
import { predictKcat, predictKcatWithFile } from '../services/api';

const KcatPredictor: React.FC = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<any>(null);
  const [error, setError] = useState<string | undefined>(undefined);

  const handleSubmit = async (sequence: string, file: File | null) => {
    setIsLoading(true);
    setError(undefined);
    setResult(null);

    try {
      // 
      // 
      const substrate = "CC(=O)Nc1ccc(O)cc1"; // SMILES
      
      let response;
      if (file) {
        // 
        response = await predictKcatWithFile(file, null);
      } else {
        // 
        response = await predictKcat(sequence, substrate);
      }

      if (response.success) {
        setResult(response.data);
      } else {
        setError(response.error);
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Container>
      <Row className="mb-4">
        <Col>
          <h2 className="text-primary">Kcat</h2>
          <p className="text-muted">
            (Kcat)
          </p>
        </Col>
      </Row>

      <Row>
        <Col lg={6}>
          <Card className="shadow-sm">
            <Card.Body>
              <Card.Title></Card.Title>
              <SequenceInput 
                onSubmit={handleSubmit} 
                isLoading={isLoading} 
                includeSubstrate={true}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col lg={6}>
          {isLoading ? (
            <div className="loading-spinner">
              <Spinner animation="border" role="status" variant="primary">
                <span className="visually-hidden">...</span>
              </Spinner>
              <p className="mt-3">...</p>
            </div>
          ) : (
            result && <ResultDisplay resultType="kcat" data={result} error={error} />
          )}
        </Col>
      </Row>

      <Row className="mt-5">
        <Col>
          <h4>Kcat</h4>
          <p>
            Kcat
            
            -Kcat
          </p>
        </Col>
      </Row>
    </Container>
  );
};

export default KcatPredictor; 