import React from 'react';
import { Card, Table, Alert } from 'react-bootstrap';

interface ResultDisplayProps {
  resultType: 'kcat' | 'km' | 'tm' | 'solubility';
  data: any;
  error?: string;
}

const ResultDisplay: React.FC<ResultDisplayProps> = ({ resultType, data, error }) => {
  if (error) {
    return <Alert variant="danger">{error}</Alert>;
  }

  if (!data) {
    return null;
  }

  const renderKcatResult = () => (
    <Card>
      <Card.Header as="h5" className="bg-primary text-white">Kcat</Card.Header>
      <Card.Body>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th> (s<sup>-1</sup>)</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{data.predicted_kcat}</td>
              <td>{data.confidence_interval}</td>
              <td>{data.reliability_score}</td>
            </tr>
          </tbody>
        </Table>
        {data.similar_enzymes && (
          <div className="mt-4">
            <h6></h6>
            <Table size="sm">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th>Kcat (s<sup>-1</sup>)</th>
                </tr>
              </thead>
              <tbody>
                {data.similar_enzymes.map((enzyme: any, idx: number) => (
                  <tr key={idx}>
                    <td>{enzyme.name}</td>
                    <td>{enzyme.similarity}%</td>
                    <td>{enzyme.kcat}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </Card.Body>
    </Card>
  );

  const renderKmResult = () => (
    <Card>
      <Card.Header as="h5" className="bg-danger text-white">Km</Card.Header>
      <Card.Body>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th> (mM)</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{data.predicted_km}</td>
              <td>{data.confidence_interval}</td>
              <td>{data.reliability_score}</td>
            </tr>
          </tbody>
        </Table>
        {data.similar_enzymes && (
          <div className="mt-4">
            <h6></h6>
            <Table size="sm">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th>Km (mM)</th>
                </tr>
              </thead>
              <tbody>
                {data.similar_enzymes.map((enzyme: any, idx: number) => (
                  <tr key={idx}>
                    <td>{enzyme.name}</td>
                    <td>{enzyme.similarity}%</td>
                    <td>{enzyme.km}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </Card.Body>
    </Card>
  );

  const renderTmResult = () => (
    <Card>
      <Card.Header as="h5" className="bg-warning text-dark">Tm</Card.Header>
      <Card.Body>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th> (C)</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{data.predicted_tm}</td>
              <td>{data.confidence_interval}</td>
              <td>{data.reliability_score}</td>
            </tr>
          </tbody>
        </Table>
        {data.protein_features && (
          <div className="mt-4">
            <h6></h6>
            <Table size="sm">
              <tbody>
                <tr>
                  <td></td>
                  <td>{data.protein_features.amino_acid_composition}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{data.protein_features.secondary_structure}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>{data.protein_features.isoelectric_point}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        )}
      </Card.Body>
    </Card>
  );

  const renderSolubilityResult = () => (
    <Card>
      <Card.Header as="h5" className="bg-success text-white"></Card.Header>
      <Card.Body>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{data.predicted_solubility}</td>
              <td>{data.solubility_score}</td>
              <td>{data.reliability_score}</td>
            </tr>
          </tbody>
        </Table>
        {data.solubility_factors && (
          <div className="mt-4">
            <h6></h6>
            <Table size="sm">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {Object.entries(data.solubility_factors).map(([key, value]: [string, any]) => (
                  <tr key={key}>
                    <td>{key}</td>
                    <td>{value.score}</td>
                    <td>{value.impact}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </Card.Body>
    </Card>
  );

  const renderResult = () => {
    switch (resultType) {
      case 'kcat':
        return renderKcatResult();
      case 'km':
        return renderKmResult();
      case 'tm':
        return renderTmResult();
      case 'solubility':
        return renderSolubilityResult();
      default:
        return <Alert variant="warning"></Alert>;
    }
  };

  return (
    <div className="result-container">
      <h4 className="mb-4"></h4>
      {renderResult()}
    </div>
  );
};

export default ResultDisplay; 