import React from 'react';
import { Container, Row, Col, Card, Tab, Nav } from 'react-bootstrap';

const Api: React.FC = () => {
  return (
    <Container>
      <Row className="mb-4">
        <Col>
          <h2 className="text-primary">API</h2>
          <p className="lead">
            API
          </p>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <Card.Body>
              <Tab.Container id="api-tabs" defaultActiveKey="overview">
                <Row>
                  <Col md={3}>
                    <Nav variant="pills" className="flex-column">
                      <Nav.Item>
                        <Nav.Link eventKey="overview"></Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="authentication"></Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="endpoints"></Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="examples"></Nav.Link>
                      </Nav.Item>
                    </Nav>
                  </Col>
                  <Col md={9}>
                    <Tab.Content>
                      <Tab.Pane eventKey="overview">
                        <h4>API</h4>
                        <p>
                          RESTful API
                          APIJSON
                        </p>
                        <p>
                          APIURL: <code>https://api.protein-predictor.example.com/v1</code>
                        </p>
                      </Tab.Pane>
                      
                      <Tab.Pane eventKey="authentication">
                        <h4></h4>
                        <p>
                          APIAPIAPI
                          API
                        </p>
                        <pre>
                          {`Authorization: Bearer YOUR_API_KEY`}
                        </pre>
                      </Tab.Pane>
                      
                      <Tab.Pane eventKey="endpoints">
                        <h4></h4>
                        <div className="mb-4">
                          <h5>Kcat</h5>
                          <code>POST /predict/kcat</code>
                          <p>:</p>
                          <pre>{`{
  "sequence": "MVKTVVTGFGAVGGGFVMRLFEQ...",
  "substrate": "CC(=O)Nc1ccc(O)cc1"
}`}</pre>
                        </div>
                        
                        <div className="mb-4">
                          <h5>Km</h5>
                          <code>POST /predict/km</code>
                          <p>Kcat</p>
                        </div>
                        
                        <div className="mb-4">
                          <h5>Tm</h5>
                          <code>POST /predict/tm</code>
                          <p>:</p>
                          <pre>{`{
  "sequence": "MVKTVVTGFGAVGGGFVMRLFEQ..."
}`}</pre>
                        </div>
                        
                        <div className="mb-4">
                          <h5></h5>
                          <code>POST /predict/solubility</code>
                          <p>Tm</p>
                        </div>
                      </Tab.Pane>
                      
                      <Tab.Pane eventKey="examples">
                        <h4></h4>
                        <p>PythonKcat</p>
                        <pre>{`import requests

api_key = "YOUR_API_KEY"
url = "https://api.protein-predictor.example.com/v1/predict/kcat"

headers = {
    "Authorization": f"Bearer {api_key}",
    "Content-Type": "application/json"
}

data = {
    "sequence": "MVKTVVTGFGAVGGGFVMRLFEQ...",
    "substrate": "CC(=O)Nc1ccc(O)cc1"
}

response = requests.post(url, json=data, headers=headers)
result = response.json()

print(result)`}</pre>
                      </Tab.Pane>
                    </Tab.Content>
                  </Col>
                </Row>
              </Tab.Container>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Api; 